import io from 'socket.io-client';
import { SocketMsgTypes } from '../constants/workerMessageTypes';
var socket = io();
socket.on('disconnect', function () { console.log('Socket.IO disconnected!'); });
var worker = self;
self.addEventListener("message", function (e) {
    var type = e.data[0];
});
// start socket server
socket.on('connect', function () {
    console.log('Socket.IO connected!');
    worker.postMessage([SocketMsgTypes.CONNECTED]);
    console.log('Get mapbox token');
    socket.emit('get_mapbox_token', {});
});
socket.on('mapbox_token', function (token) {
    console.log('token Got:' + token);
    worker.postMessage([
        SocketMsgTypes.NOTIFY_MAPBOX_TOKEN,
        token
    ]);
    startRecivedData();
});
var toArrayColor = function (color) {
    return [
        ((color & 0xFF0000) >> 16),
        ((color & 0x00FF00) >> 8),
        (color & 0x0000FF)
    ];
};
var getColor = function (index) {
    var red = 0;
    var green = 0;
    var blue = 0;
    index++;
    var ratio = Math.floor(index / 3);
    if (index % 3 === 0) {
        green = 0xff - 0x11 * ratio;
    }
    else if (index % 3 === 1) {
        blue = 0xff - 0x11 * ratio;
    }
    else {
        red = 0xff - 0x11 * ratio;
    }
    return [red, green, blue];
};
var createGradientColorGenerator = function (min, max) {
    var baseblue = 255;
    var basered = 0;
    var ratio = 255 / (max - min);
    return function (value) {
        var v = value > max ? max : value < min ? min : value;
        var blue = baseblue - ratio * (v - min);
        var red = basered + ratio * (v - min);
        return [Math.floor(red), 0, Math.floor(blue)];
    };
};
var getData = function (bar) {
    var time = bar.ts.seconds;
    var barType = bar.type;
    var isFixColor = barType === 0 || barType === 2;
    var isHexa = barType === 2 || barType === 3;
    var colorGenerator = createGradientColorGenerator(bar.min, bar.max);
    return {
        id: bar.id,
        movesbaseidx: bar.id,
        sourcePosition: [],
        sourceColor: [],
        targetPosition: [],
        targetColor: [],
        elapsedtime: time,
        position: [bar.lon, bar.lat, 0],
        angle: 0,
        speed: 0,
        shapeType: isHexa ? 6 : 4,
        areaColor: toArrayColor(bar.color),
        data: bar.barData.map(function (b, index) {
            var color = b.color;
            return {
                value: b.value,
                color: isFixColor ? toArrayColor(color) : colorGenerator(b.value),
                label: b.label,
            };
        }),
        radius: bar.radius,
        width: bar.width,
        min: bar.min,
        max: bar.max,
        text: bar.text,
    };
};
function startRecivedData() {
    socket.on('bargraphs', function (str) {
        var rawData = JSON.parse(str);
        var bars = rawData.bars;
        var data = bars.map(function (b) { return getData(b); });
        worker.postMessage([SocketMsgTypes.RECIVED_BAR_GRAPHS, data]);
    });
}
